/* Start Global Rules */
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
}
:root {
  --main-color: #40b957;
  --main-transition: 0.2s all linear;
  --main-p-color: #888888;
  --main-border-bottom: 1px solid #eaeaea;
  --main-h-color: #333;
}
html {
  scroll-behavior: smooth;
}
body::before {
  content: "";
  background-color: black;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 111;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s linear;
}
body.active::before {
  visibility: visible;
  opacity: 0.5;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
.effect {
  visibility: hidden;
  opacity: 0;
  transform: translateY(100px);
  transition: 0.3s all linear;
}
.effect.active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}
.container {
  padding: 0 100px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 50px;
  }
}
.sections-wrapper {
  padding-left: 365px;
}
@media (max-width: 1199px) {
  .sections-wrapper {
    padding: 0;
  }
}
/* @media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
} */
::selection {
  color: white;
  background: var(--main-color);
}
/* End Global Rules */
/* Start Scroll Button */
button.scroll-button {
  position: fixed;
  right: 50px;
  bottom: 50px;
  background-color: var(--main-color);
  border: none;
  width: 55px;
  height: 55px;
  border-radius: 50%;
  z-index: 111111111;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s all linear;
  color: white;
  font-size: 20px;
  cursor: pointer;
}
@media (max-width: 767px) {
  button.scroll-button {
    right: 30px;
    bottom: 30px;
  }
}
button.scroll-button:hover {
  background-color: #309543;
}
button.scroll-button.active {
  visibility: visible;
  opacity: 1;
}
/* End Scroll Button */
/* Start Navbar */
nav {
  background-color: #333333;
  position: fixed;
  left: 0;
  height: 100%;
  width: 365px;
  color: white;
  z-index: 11111;
  transition: var(--main-transition);
}
@media (max-width: 1199px) {
  nav {
    left: -120%;
  }
  nav.active {
    left: 0px;
  }
}
nav .nav-header .info-header {
  background-color: #2e2e2e;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 35px;
}
nav .nav-header .info-header a {
  display: flex;
  align-items: center;
  gap: 5px;
  color: white;
  font-size: 14px;
}
nav .nav-header .info-header a:hover {
  color: var(--main-color);
}
nav .nav-header .info-header .languages {
  font-size: 14px;
  position: relative;
}
nav .nav-header .info-header .languages button {
  cursor: pointer;
  border: none;
  background-color: transparent;
  color: white;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 5px;
}
nav .nav-header .info-header .languages button svg {
  font-size: 12px;
}
nav .nav-header .info-header .languages ul {
  background-color: #fff;
  color: black;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  visibility: hidden;
  opacity: 0;
  top: 50px;
  transition: var(--main-transition);
  z-index: 9999;
}
nav .nav-header .info-header .languages ul.active {
  visibility: visible;
  opacity: 1;
  top: 30px;
}
nav .nav-header .info-header .languages ul li {
  cursor: pointer;
  padding: 5px 30px;
}
nav .nav-header .info-header .languages ul li:hover {
  color: var(--main-color);
}
nav .nav-header .info-header .languages ul li:not(:last-child) {
  border-bottom: var(--main-border-bottom);
}
nav .nav-header .main-header {
  padding: 35px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
nav .nav-header .main-header .icons {
  display: flex;
  align-items: center;
  gap: 15px;
}
nav .nav-header .main-header .icons button {
  color: white;
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 25px;
  position: relative;
}
nav .nav-header .main-header .icons button .count-container {
  position: absolute;
  top: -5px;
  font-size: 13px;
  right: -5px;
  background-color: var(--main-color);
  width: 18px;
  height: 18px;
  border-radius: 50%;
}
nav .nav-header .main-header .logo {
  font-size: 35px;
  font-weight: bold;
  letter-spacing: 2px;
  color: white;
}
nav .nav-header form {
  background-color: #484848;
  margin: 0 35px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 15px;
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}
nav .nav-header form input {
  background-color: transparent;
  border: none;
  font-size: 15px;
}
nav .nav-header form input:focus {
  outline: none;
  color: white;
}
nav .nav-header form button {
  background-color: transparent;
  border: none;
  color: white;
  font-size: 20px;
  cursor: pointer;
}
nav ul.nav-body {
  padding: 35px 35px 0 35px;
}
nav ul.nav-body li {
  padding: 10px 0;
  cursor: pointer;
}
nav ul.nav-body li:not(:last-child) {
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
nav ul.nav-body li a {
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  transition: var(--main-transition);
}
nav ul.nav-body li:hover a {
  color: var(--main-color);
}
nav ul.nav-body li:first-child a {
  color: var(--main-color);
}
nav ul.nav-social {
  padding: 0 35px;
  display: flex;
  align-items: center;
  gap: 20px;
  position: absolute;
  bottom: 10px;
}
nav ul.nav-social li a {
  font-size: 22px;
  color: white;
}
nav ul.nav-social li a:hover {
  color: var(--main-color);
  transition: var(--main-transition);
}
/* End Navbar */
/* Start Menu */
ul.menu {
  z-index: 111111111111111;
  text-align: center;
  visibility: hidden;
  opacity: 0;
  background-color: var(--main-h-color);
  position: fixed;
  width: 100%;
  padding-top: 50px;
  top: -100%;
  transition: 0.4s all linear;
}
ul.menu button {
  background-color: transparent;
  border: none;
  font-size: 25px;
  color: var(--main-color);
  cursor: pointer;
  position: absolute;
  top: 10px;
  left: 10px;
}
ul.menu li {
  padding: 10px 0;
  cursor: pointer;
  transition: var(--main-transition);
}
ul.menu li:hover {
  background-color: var(--main-color);
}
ul.menu li:not(:last-child) {
  border-bottom: 1px solid #454545;
}
ul.menu li a {
  color: white;
  font-weight: bold;
  text-transform: uppercase;
}
ul.menu li:hover a {
  color: white;
}
ul.menu li:last-child a {
  font-size: 25px;
}
ul.menu.active {
  visibility: visible;
  opacity: 1;
  top: 0;
}
/* End Menu */
/* Start Cart */
.cart {
  width: 360px;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0px;
  padding: 20px;
  transition: var(--main-transition);
  background-color: white;
  z-index: 1111;
  box-shadow: 15px 0 20px -20px rgba(0, 0, 0, 0.45);
}
@media (max-width: 1199px) {
  .cart {
    left: -120%;
  }
}
.cart .cart-header h3 {
  color: rgba(0, 0, 0, 0.883);
  text-transform: uppercase;
  font-size: 14px;
  border-bottom: var(--main-border-bottom);
  padding-bottom: 10px;
}
.cart .cart-header p {
  color: var(--main-p-color);
  padding: 20px 0;
  font-size: 14px;
}
.cart.active {
  left: 365px;
}
/* End Cart */
/* Start Landing */
section.landing {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  background-color: #e4e4e4;
  background-image: url("landing.jpg");
}
section.landing button.menu-button {
  background-color: transparent;
  border: none;
  width: fit-content;
  font-size: 30px;
  cursor: pointer;
  color: var(--main-h-color);
  position: absolute;
  left: 40px;
  top: 40px;
  z-index: 11111111;
}
section.landing .container {
  padding: 0 60px;
}
section.landing p {
  opacity: 0.8;
  font-size: 15px;
  margin-bottom: 10px;
}
section.landing h2 {
  color: #333333;
  font-size: 60px;
  max-width: 700px;
  font-weight: 900;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  section.landing h2 {
    font-size: 40px;
  }
}
section.landing h2 span {
  color: var(--main-color);
}
section.landing .buttons {
  display: flex;
  align-items: center;
  gap: 10px;
}
section.landing button.buy-button {
  border: 1px solid black;
  border-radius: 5px;
  background-color: #e4e4e4;
  text-transform: uppercase;
  font-weight: bold;
  color: #333333;
  padding: 10px 30px;
  cursor: pointer;
  letter-spacing: 2px;
  font-size: 15px;
}
section.landing button.buy-button:hover {
  background-color: white;
}
section.landing button.read-button {
  border: none;
  border-radius: 5px;
  background-color: var(--main-color);
  text-transform: uppercase;
  font-weight: bold;
  color: white;
  padding: 11px 30px;
  cursor: pointer;
  letter-spacing: 2px;
  font-size: 15px;
}
section.landing button.read-button:hover {
  background-color: white;
  color: black;
}
@media (max-width: 767px) {
  section.landing .buttons button {
    padding: 0;
    width: 120px;
    height: 40px;
    font-size: 12px;
  }
}
/* End Landing */
/* Start Features */
section.features-section {
  padding-top: 100px;
}
section.features-section .header {
  text-align: center;
}
section.features-section .header h1 {
  color: var(--main-h-color);
  font-size: 42px;
  margin-bottom: 30px;
}
@media (max-width: 767px) {
  section.features-section .header h1 {
    font-size: 35px;
  }
}
section.features-section .header p {
  font-weight: bold;
  color: var(--main-p-color);
  margin-bottom: 30px;
}
section.features-section .video {
  background-color: #2e2e2e;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  gap: 20px;
  background-image: url("landing.jpg");
  background-position: center;
  height: 473px;
  margin-bottom: 100px;
}
@media (max-width: 767px) {
  section.features-section .video {
    flex-direction: column;
  }
}
section.features-section .video svg {
  font-size: 100px;
  cursor: pointer;
  transition: 0.5s all linear;
}
section.features-section .video svg:hover {
  color: var(--main-color);
}
section.features-section .video .text {
  text-align: center;
}
section.features-section .video .text h1 {
  font-size: 70px;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  section.features-section .video .text h1 {
    font-size: 50px;
  }
}
section.features-section .video .text p {
  margin-top: -20px;
}
@media (max-width: 767px) {
  section.features-section .video .text p {
    margin-top: 0;
  }
}
section.features-section .features {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  gap: 80px;
  border-bottom: 1px dashed #eaeaea;
  padding-bottom: 100px;
}
section.features-section .features .feature {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  max-width: 300px;
}
section.features-section .features .feature svg {
  font-size: 50px;
}
section.features-section .features .feature h3 {
  color: var(--main-h-color);
}
section.features-section .features .feature p {
  color: var(--main-p-color);
  font-size: 14px;
}
/* End Features */
/* Start Intro */
section.intro {
  padding: 100px 0;
}
section.intro .container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  gap: 100px;
}
@media (min-width: 768px) {
  section.intro .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  section.intro .container {
    width: 970px;
  }
}
@media (min-width: 992px) {
  section.intro .container {
    width: 850px;
  }
}
@media (max-width: 767px) {
  section.intro .container .row {
    flex-direction: column;
    text-align: center;
  }
}
section.intro .row {
  display: flex;
  align-items: center;
  gap: 50px;
}
section.intro .row img {
  max-width: 50%;
}
section.intro .row .text {
  max-width: 500px;
}
section.intro .row .text h4 {
  font-weight: bold;
  color: var(--main-color);
  font-size: 20px;
  margin-bottom: 15px;
}
section.intro .row .text h3 {
  font-size: 40px;
  margin-bottom: 10px;
  font-weight: 400;
}
section.intro .row .text p {
  color: var(--main-p-color);
  font-size: 14px;
  line-height: 2;
}
/* End Intro */
/* Start Quotations */
.quotations {
  text-align: center;
  width: 100%;
  background-color: #e4e4e4;
  padding: 60px;
  color: var(--main-h-color);
  /* background-image: url("landing.jpg"); */
}
@media (max-width: 767px) {
  .quotations .container {
    padding: 0;
  }
}
.quotations h4 {
  font-weight: 400;
  max-width: 600px;
  margin: auto;
  padding-bottom: 20px;
  font-size: 20px;
  line-height: 1.6;
  position: relative;
}
@media (max-width: 767px) {
  .quotations h4 {
    font-size: 15px;
  }
}
.quotations h4::before {
  position: absolute;
  content: "";
  height: 2px;
  width: 100px;
  left: 50%;
  bottom: -1px;
  margin-left: -50px;
  background: #40b957;
}
.quotations p {
  margin-top: 20px;
}
@media (max-width: 767px) {
  .quotations p {
    font-size: 15px;
  }
}
.quotations p span {
  font-weight: bold;
}
/* End Quotations */
/* Start Products */
section.products {
  padding: 100px 0;
}
section.products .container .header {
  text-align: center;
  margin-bottom: 30px;
  font-size: 25px;
  color: var(--main-h-color);
}
section.products .container .products-container {
  display: flex;
  justify-content: center;
  overflow: hidden;
  gap: 30px;
}
section.products .container .products-container .product {
  border: var(--main-border-bottom);
  border-radius: 5px;
  cursor: pointer;
  transition: var(--main-transition);
  width: 262px;
}
section.products .container .products-container .product .image {
  position: relative;
}
section.products .container .products-container .product .image img {
  width: 260px;
}
section.products .container .products-container .product .image span {
  position: absolute;
  color: white;
  background-color: var(--main-color);
  border-radius: 50%;
  padding: 15px 10px;
  font-size: 14px;
  right: 10px;
  top: 10px;
}
section.products .container .products-container .product:hover {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 5px 8px;
}
section.products .container .products-container .product .product-info {
  padding: 10px;
  position: relative;
}
section.products .container .products-container .product .product-info .rating {
  color: #ccc;
  margin-bottom: 10px;
}
section.products .container .products-container .product .product-info h4 {
  max-width: 300px;
  font-weight: 400;
  font-size: 14px;
  color: var(--main-h-color);
  margin-bottom: 10px;
}
section.products
  .container
  .products-container
  .product
  .product-info
  span.old-price {
  font-weight: bold;
  text-decoration: line-through;
  color: #888;
  margin-right: 5px;
}

section.products
  .container
  .products-container
  .product
  .product-info
  span:last-of-type {
  font-size: 17px;
  font-weight: bold;
  color: #e12020;
}
section.products
  .container
  .products-container
  .product
  .product-info
  button.cart-add-button {
  background-color: transparent;
  border: none;
  color: white;
  background-color: #333333;
  border-radius: 3px;
  cursor: pointer;
  width: 35px;
  height: 35px;
  font-size: 17px;
  color: white;
  transition: var(--main-transition);
  position: absolute;
  right: 10px;
  bottom: 10px;
}
section.products
  .container
  .products-container
  .product
  .product-info
  button.cart-add-button:hover {
  color: var(--main-color);
}
/* End Products */
/* Start Details */
.sections-wrapper.product-details {
  padding: 30px 0 0 395px;
}
@media (max-width: 1199px) {
  .sections-wrapper.product-details {
    padding: 30px 0 0 20px;
  }
}
@media (max-width: 767px) {
  .sections-wrapper.product-details {
    padding: 20px;
  }
}
.sections-wrapper.product-details a.back {
  color: #e12020;
  font-size: 30px;
}
.page {
  display: flex;
  align-items: flex-start;
  gap: 20px;
}
@media (max-width: 767px) {
  .page {
    flex-direction: column;
  }
}
.page .image-slider {
  border: 1px solid #e12020;
  width: 10%;
  cursor: pointer;
}
.page .image-slider img {
  width: 100%;
}
.page .image {
  border: var(--main-border-bottom);
  width: 50%;
  cursor: grab;
}
@media (max-width: 767px) {
  .page .image {
    width: 100%;
  }
}
.page .image img {
  width: 100%;
}
.page .details {
  width: 50%;
}
@media (max-width: 767px) {
  .page .details {
    width: 100%;
    text-align: center;
  }
}
.page .details h2 {
  color: var(--main-h-color);
  font-size: 1.7em;
  margin-bottom: 15px;
  line-height: 1.1;
}
.page .details span {
  color: #e12020;
  font-weight: bold;
  font-size: 1.7em;
  margin-bottom: 10px;
}
.page .details .rating {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
@media (max-width: 767px) {
  .page .details .rating {
    justify-content: center;
  }
}
.page .details .rating svg {
  color: #ccc;
  cursor: pointer;
}
.page .details .rating span {
  font-size: 15px;
  font-weight: 400;
  margin-left: 10px;
  margin-bottom: 0;
}
.page .details p {
  color: #888;
  font-size: 14px;
  margin-bottom: 15px;
}
.page .details .options {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .page .details .options {
    justify-content: center;
  }
}
.page .details .options input {
  width: 70px;
  height: 50px;
  border: var(--main-border-bottom);
  border-radius: 5px;
  font-size: 15px;
  padding: 0 20px;
  color: var(--main-p-color);
}
.page .details .options input:focus {
  outline: none;
}
.page .details .options button:first-of-type {
  background-color: #e12020;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  padding: 0 20px;
  height: 50px;
}
.page .details .options button:last-of-type {
  transition: var(--main-transition);
  border: 1px solid #888;
  border-radius: 5px;
  width: 50px;
  height: 50px;
  cursor: pointer;
  color: white;
  font-size: 25px;
  position: relative;
  color: var(--main-h-color);
  background-color: white;
}
.page .details .options button:last-of-type:hover {
  background-color: #e12020;
  color: white;
  border-color: #e12020;
}
.page .details .options button:last-of-type svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.page .details h6 {
  font-size: 13px;
  margin-bottom: 5px;
}
.page .details h6 a {
  color: #888;
  font-weight: normal;
  transition: var(--main-transition);
}
.page .details h6 a:hover {
  color: #e12020;
}
.page .details .social-media {
  margin-bottom: 15px;
}
.page .details .social-media p {
  color: var(--main-h-color);
}
.page .details .social-media ul {
  display: flex;
  align-items: center;
  gap: 10px;
}
@media (max-width: 767px) {
  .page .details .social-media ul {
    justify-content: center;
  }
}
.page .details .social-media ul li a img {
  width: 40px;
}
/* End Details */
/* Start Sign Up */
.sign-up {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  padding: 50px 0 70px 0;
}
@media (max-width: 991px) {
  .sign-up {
    flex-direction: column;
  }
}
.sign-up a {
  font-weight: bold;
  color: var(--main-h-color);
  font-size: 14px;
}
@media (max-width: 767px) {
  .sign-up form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
  }
}
.sign-up form input {
  min-width: 380px;
  border: 1px solid #333;
  padding: 7px 15px;
}
@media (max-width: 767px) {
  .sign-up form input {
    min-width: 330px;
  }
}
.sign-up form input:focus {
  outline: none;
}
.sign-up form button {
  background: #333;
  border: 1px solid #333;
  color: #fff;
  padding: 7px 30px;
  cursor: pointer;
}
/* End Sign Up */
/* Start Footer */
.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  background-color: #333333;
  padding: 50px;
  text-align: center;
  gap: 10px;
}
@media (max-width: 767px) {
  .footer {
    gap: 20px;
  }
}
.footer ul {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
}
@media (max-width: 767px) {
  .footer ul {
    gap: 10px;
  }
}
.footer ul li {
  padding: 0 20px;
}
@media (max-width: 767px) {
  .footer ul li {
    padding: 0 10px;
  }
}
.footer ul li:not(:last-child) {
  border-right: 1px solid white;
}
@media (max-width: 767px) {
  .footer ul li:not(:last-child) {
    border-right: none;
  }
}
.footer ul li a {
  color: white;
  font-size: 14px;
  transition: var(--main-transition);
}
@media (max-width: 767px) {
  .footer ul li a {
    font-size: 13px;
  }
}
.footer ul li a:hover {
  color: var(--main-color);
}
.footer p {
  color: #ccc;
  font-size: 13.5px;
  margin-bottom: 15px;
}
.footer p a {
  color: var(--main-color);
}
/* End Footer */
